<template>
    <div>
        <p class='H2'>Input parameters (Required):</p>
        <el-table class='Table' :data="tableData">   
            <el-table-column label="ALT(IU/L)" align="center"> 
                <template slot-scope="scope">         
                    <el-input v-model="scope.row.ALT" placeholder="204" clearable />
                </template>
            </el-table-column>
            <el-table-column label="AST(IU/L)" align="center"> 
                <template slot-scope="scope">         
                    <el-input v-model="scope.row.AST" placeholder="220" clearable />
                </template>
            </el-table-column>
            <el-table-column label="GLB(g/L)" align="center">
                <template slot-scope="scope"> 
                    <el-input v-model="scope.row.GLB" placeholder="25" clearable />
                </template>
            </el-table-column>
            <el-table-column label="PLT(X10^9)" align="center">
                <template slot-scope="scope"> 
                    <el-input v-model="scope.row.PLT" placeholder="112" clearable />
                </template>
            </el-table-column>
            <el-table-column label="ANA" align="center">
                <template slot-scope="scope"> 
                    <el-input v-model="scope.row.ANA" placeholder="4" clearable />
                </template>
            </el-table-column>
            <el-table-column label="IgG(mg/dL)" align="center">
                <template slot-scope="scope">   
                    <el-input v-model="scope.row.IgG" placeholder="2375"/>
                </template>   
            </el-table-column>
            <!-- <el-table-column label="Option" align="center">
                <el-button size="mini" type="info" @click="addRow()">Add</el-button>
            </el-table-column> -->
        </el-table>
        <br />
        <div class='Submit'>
            <el-button @click="submit" type="primary">Submit</el-button>
        </div>
    </div>
</template>

<script>
import {nanoid} from 'nanoid'
import httptool from '../http'

export default {
    name: 'AidTable',
     data () {
        return {
        predict:["null"],
        tableData: [{
            id:nanoid(),
            ALT: 204,
            AST: 220,
            GLB: 25,
            PLT: 112,
            ANA: 4,
            IgG: 2375,
            }],
        search: ''
        }
    },
    methods:{
        addRow() {
        const newLine = {
            id:'',
            ALT: '',
            AST:'',
            GLB: '',
            PLT: '',
            ANA: '',
            IgG: '',
        }
        this.index++
        this.tableData.push(newLine)
        },
        submit(){
            // console.log(this.tableData[0]);
            let temp_data=this.tableData[0]
            httptool.get('predict',{
                params:{
                    id:temp_data.id,
                    IgG:temp_data.IgG,
                    AST:temp_data.AST,
                    ALT:temp_data.ALT,
                    ANA:temp_data.ANA,
                    GLB:temp_data.GLB,
                    PLT:temp_data.PLT,
                },
                headers:{"platform":"bjaid"}
            }).then(response=>{
                // console.log(response.data);
                this.predict=response.data;
                if (this.predict!=undefined){
                    this.$bus.$emit('update_result',this.predict,this.tableData)
                    this.$bus.$emit('update_chart',this.predict, this.tableData)
                }
                // console.log(this.predict.prob)
                // console.log(this.predict.result)
            }).catch(error=>{
                console.log(error);
                console.log(error.response);
            })
        },
    },
}
</script>

<style scoped>
    .Submit{
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .Table{
        width: 98%; 
        margin-left:20px
    }

    .H2{
        text-align:left; 
        font-family: TimesNewRoman;
        font-weight: bold;
    }

</style>