<template>
    <div>
        <p class='H2'>Calculated Result</p>
        <el-row :gutter="20">
            <el-col :span="4">
                <p :inline="true">Predicted Result:</p></el-col>
            <el-col :span="4">
                <el-input :inline="true" v-model="result.result"></el-input></el-col>
            <el-col :span="5" :offset="3">
                <p :inline="true">Confidence:</p></el-col>
            <el-col :span="4">
                <el-input :inline="true" v-model="result.prob"></el-input></el-col>
        </el-row>
        <AidEcharts/>
    </div>
</template>

<script>
import AidEcharts from '../components/AidParallelGraph.vue'
export default {
    name: 'AidResult',
    data(){
        return{
            result:[],
        }
    },
    methods:{
        update_result(data, input){
            if(data.nid===input[0].id){
                this.result=data;
            }else{
                alert("id doest not match")
                console.log("data_id:", data.nid);
                console.log("input_id:", input[0].nid);
            }
            
        }
    },
    components:{
    AidEcharts
    },
    mounted(){
        this.$bus.$on('update_result', this.update_result);
    },
    beforeDestroy(){
        this.$bus.$off('update_result');
    },
}
</script>

<style scoped>
    .H2{
        text-align:left; 
        font-family: TimesNewRoman;
        font-weight: bold;
    }

    .Submit{
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

</style>